.render {
    width: 90vw;
    iframe{
        border-radius: 2rem;
        border: 2px solid $grisOscuroT;
        box-shadow: 0 0 1rem $black;
    }
}
//mediaQuerys
@include tablet {
    .contenedor {
        .render {
            padding-top: 20rem;
            
        }
    }
}
@include telefono {
    .contenedor {
        display: flex;
        align-items: center;
       
        .render {
            iframe {
                height: 600px;
            }
        }
    }
}