.header {
    width: 100%;
    height: 80px;
    background-color: $white;
    backdrop-filter: blur(1rem);
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    z-index: 200;
    top: 0;
    padding: 0 1rem;
    .logo {
        height:80px;
        
        img {
            height:100%;
        }
    }
}
.icon_menu {
    width: 3rem;
    margin-right: 3rem;
    height: 100%;
    display: none;
    justify-content: center;
    align-items: center;
    
    i{
        font-size: 2.7rem;
        cursor: pointer;
        position: absolute;
        color: $red;
    }
}
.menu {
    display: flex;
    justify-content:space-between ;
    transition: all 300ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
}
.options {
    display: flex;
    .activ {
        .option {
            p {
                color: $white;
                font-size: 2rem;
                font-weight:$bold;
            }
        }
        background-color: $red;
        border-radius: .5rem;
        backdrop-filter: blur(1rem);
        transition: color 300ms;
    }
    .option {
        padding-left: 2rem;
        padding-right: 2rem;
        display: flex;
        align-items: center;
        position: relative;
        height: 4rem;
        p {
            color: $red;
            font-weight: $bold;
            cursor: pointer;
            font-size: 2rem;
        }
        &:hover{
            p {
                color: $white;
                font-size: 2rem;
                font-weight:$bold;
            }
            background-color: $red;
            border: .1rem solid $grisOscuro;
            border-radius: .5rem;
            backdrop-filter: blur(1rem);
            transition: color 300ms;
        }
    }
}
@include tablet {
    .header {
        padding: 0 1rem;
        .option {
            padding: 0 1rem 0 1rem ;
        }
    }
}
@include telefono {
    .header {
        .menu {
            height: 0vh;
            width: 100vw;
            position: absolute;
            top: 80px;
            left: 0;
            right: 0;
            background: $grisMediumT;
            backdrop-filter: blur(1rem);
            transition: 1.5s;
            overflow: hidden;
            p {
                font-size: 3rem;
                color: $red;
            }
            .options {
                display: block;
                width: fit-content;
                margin: 30vh auto 0 auto;
                text-align: center;
                .activ {
                    color: $grisClaro;
                    .option{
                        background-color: $red;
                        border-radius: .5rem;
                        p {
                            font-size: 3rem;
                        }
                    }   
                }
                .option {
                    margin-bottom: 3rem;
                }
            }
        }
        .active {
            opacity: 1;
            transition: 1.5s;
            height: 100vh;  
        }
        .icon_menu {
            display: inline-flex;
            
        }
    }
}
